import React from 'react'
import './electricitéIncre.css'
import Header from '../../../../../common/heading/Header'
import Footer from '../../../../../footer/Footer'
import { useDispatch, useSelector } from 'react-redux';
function ElectricitéIncre() {

    const gainableCount = useSelector(state => state.user.gainableCount); 
    const splitCount = useSelector(state => state.user.splitCount); 
    const dispatch = useDispatch();

    const handleWhatsappButtonClick = () => {
        window.open('https://api.whatsapp.com/send?phone=774463661','_blank');
    };

  return (
    <div>
            <Header />
            <div className='electricitéIncreTop'>
                <h1>Electricité</h1>
                <p>Où est localisée la panne ?</p>
                <hr />
                <div className='electricitéIncredisplayParagraphe'>
                    <div className='electricitéIncredisplayIncre'>
                        <p>Le disjoncteur saute </p>
                        <div className='electricitéIncredisplaybuttonIncre'>
                            <button onClick={() => dispatch(decrementGainable())} style={{ backgroundColor: 'rgba(161, 118, 88, 0.437)' }} className='electricitéIncrebuttonIncre'> - </button>
                            <p>{gainableCount}</p>
                            <button onClick={() => dispatch(incrementGainable())} style={{ backgroundColor: 'rgb(161, 119, 88)' }} className='electricitéIncrebuttonIncre'>+</button>
                        </div>
                    </div>
                    <div className='electricitéIncredisplayIncre'>
                        <p>Je veux vérifier mon installation </p>
                        <div className='electricitéIncredisplaybuttonIncre'>
                            <button onClick={() => dispatch(decrementSplit())} style={{ backgroundColor: 'rgba(161, 118, 88, 0.437)' }} className='electricitéIncrebuttonIncre'> - </button>
                            <p>{splitCount}</p>
                            <button onClick={() => dispatch(incrementSplit())} style={{ backgroundColor: 'rgb(161, 119, 88)' }} className='electricitéIncrebuttonIncre'>+</button>
                        </div>
                    </div>
                </div>
                <button className='electricitéIncrebuttonIncre2' onClick={handleWhatsappButtonClick}>ontinuer sur  Whatsapp</button>
            </div>
            <Footer />
        </div>
  )
}

export default ElectricitéIncre